<template>
    <div class="com-container">
        <div class="com-chart" ref="blog_ref">
        </div>
    </div>
</template>

<script>
export default {
    props: ['psMsg'],
    data() {
        return {
            countByMonth: [],
            commentCountByMonth: [],
            chartInstance: null,
            xAxisData: [],
            seriesData: [],
            timeList: [],
            selectList: ['','','',''],
        }
    },
    mounted() {
        this.initChart()
        this.getViewsData()
        window.addEventListener('resize', this.screenAdapter)
    },
    watch: {
        'psMsg': {
            handler(value) {
                if (value === 0) {
                    this.getViewsData()
                } else if (value === 1) {
                    this.getBlogData()
                } else if (value === 2) {
                    this.getAppreciateCountByMonth()
                } else if(value === 3){
                    this.getCommentCountByMonth()
                }
            }
        }
    },
    methods: {
        initChart() {
            this.chartInstance = this.$echarts.init(this.$refs.blog_ref, this.theme)
            const initOption = {
                backgroundColor: '#fff',

                legend: {
                    // orient 'horizontal'  'vertical'
                    orient: 'horizontal',
                    // x 'center'  'left'  'right'  {number}xpx
                    x: 'center',
                    // y 'top'  'bottom'  'center'  {number}ypx
                    y: 'top',
                    data: ['']
                },

                //  ,'' {number}px
                grid: {
                    top: '3%',   //  y: '16%'
                    left: '3%',
                    right: '3%',
                    bottom: '3%',
                    containLabel: true
                },

                // 
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross'
                    },
                    padding: [5, 10]
                },
                axisPointer: { // 
                    type: 'shadow' // 'line' | 'shadow'
                },
                xAxis: {
                    name: '',
                    type: 'category',
                    // X
                    axisLabel: {
                        rotate: 20, // 
                        interval: 0  //X0
                    },
                    // boundaryGapfalsey
                    boundaryGap: false,
                    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                },

                yAxis: {
                    name: '',
                    type: 'value',
                },

                series: [
                    {
                        name: '',
                        data: [820, 932, 301, 1434, 1290, 1330, 1320],
                        type: 'line',
                        // 
                        // symbol: 'none'
                        symbol: 'none',
                        // 0-1
                        smooth: 0.5,
                        itemStyle: {
                            normal: {
                                color: '#3888fa',
                                lineStyle: {
                                    color: '#3888fa',
                                    width: 2
                                },
                                areaStyle: {
                                    color: '#f3f8ff'
                                }
                            }
                        },
                        animationDuration: 2800,
                        animationEasing: 'cubicInOut'
                    },

                ],
            }
            this.chartInstance.setOption(initOption)
        },
        async getViewsData() {
            const {data: res} = await this.$blog.get('/admin/getViewCountByMonth')
            let list = []
            res.data.forEach((item) => {
                let ls = item.split(',')
                list = list.concat({date: ls[0], views: ls[1]})
            })
            this.countByMonth = list
            this.updateChart()
        },
        async getBlogData() {
            const {data: res} = await this.$blog.get('/admin/getBlogCountByMonth')
            let list = []
            res.data.forEach((item) => {
                let ls = item.split(',')
                list = list.concat({date: ls[0], views: ls[1]})
            })
            // console.log(res.data)
            this.countByMonth = list
            this.updateChart()
        },
        async getAppreciateCountByMonth() {
            const {data: res} = await this.$blog.get('/admin/getAppreciateCountByMonth')
            let list = []
            res.data.forEach((item) => {
                let ls = item.split(',')
                list = list.concat({date: ls[0], views: ls[1]})
            })
            this.countByMonth = list
            // console.log(res.data)
            this.updateChart()
        },
        async getCommentCountByMonth() {
            const {data: res} = await this.$blog.get('/admin/getCommentCountByMonth')
            let list = []
            res.data.forEach((item) => {
                let ls = item.split(',')
                list = list.concat({date: ls[0], views: ls[1]})
            })
            this.countByMonth = list
            // console.log(res.data)
            this.updateChart()
        },
        dateFormat2(originVal) {
            const dt = new Date(originVal)
            const y = dt.getFullYear()
            const m = (dt.getMonth() + 1 + '').padStart(2, '0')
            return `${y}-${m}`
        },
        getTimeList() {
            let time = new Date()
            let month = time.getMonth()
            let year = time.getFullYear()
            let dateList = []
            for (let i = 0; i < 7; i++) {
                dateList = dateList.concat(this.dateFormat2(time))
                if (month === 0) {
                    month = 11;
                    year = year - 1;
                } else {
                    month = month - 1;
                }
                time.setFullYear(year)
                time.setMonth(month)
            }
            this.timeList = dateList.reverse()
        },
        updateChart() {
            this.getTimeList()
            this.xAxisData = this.timeList
            let sl = []
            for (const item of this.timeList) {
                let a = this.countByMonth.find(i => i.date === item)
                sl = sl.concat(a === undefined ? 0 : a.views)
            }
            this.seriesData = sl
            const dataOption = {
                legend: {
                    data: [this.selectList[this.psMsg]]
                },
                xAxis: {
                    data: this.xAxisData
                },
                series: [
                    {
                        name: this.selectList[this.psMsg],
                        data: this.seriesData
                    }
                ]
            }
            this.chartInstance.setOption(dataOption)
        },
        screenAdapter() {
            this.chartInstance.resize()
        },
    }
}
</script>

<style scoped lang="less">

    .com-container {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .com-chart {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    canvas {
        border-radius: 20px;
    }
</style>